

import { BooleanInput } from '@angular/cdk/coercion';
import { ESCAPE, hasModifierKey } from '@angular/cdk/keycodes';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  AfterViewInit,
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  SimpleChanges,
  ViewContainerRef
} from '@angular/core';

import { BehaviorSubject, combineLatest, EMPTY, fromEvent, merge, Subject } from 'rxjs';
import { auditTime, distinctUntilChanged, filter, map, mapTo, switchMap, takeUntil, tap } from 'rxjs/operators';
import { POSITION_MAP } from 'tds-ui/core/overlay';
import { IndexableObject } from 'tds-ui/core/util';
import { InputBoolean } from 'tds-ui/shared/utility';

import { TDSDropdownMenuComponent, TDSPlacementType } from './dropdown-menu.component';

const listOfPositions = [POSITION_MAP.bottomLeft, POSITION_MAP.bottomRight, POSITION_MAP.topRight, POSITION_MAP.topLeft];

@Directive({
  selector: '[tds-dropdown]',
  exportAs: 'tdsDropdown'
})
export class TDSDropDownDirective implements AfterViewInit, OnDestroy, OnChanges, OnInit {
  static ngAcceptInputType_backdrop: BooleanInput;
  static ngAcceptInputType_hasBackdrop: BooleanInput;
  static ngAcceptInputType_clickHide: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_visible: BooleanInput;

  private portal?: TemplatePortal;
  private overlayRef: OverlayRef | null = null;
  private destroy$ = new Subject<void>();
  private positionStrategy = this.overlay
    .position()
    .flexibleConnectedTo(this.elementRef.nativeElement)
    .withLockedPosition()
    .withTransformOriginOn('.tds-dropdown')
    ;
  private inputVisible$ = new BehaviorSubject<boolean>(false);
  private trigger$ = new BehaviorSubject<'click' | 'hover'>('hover');
  private overlayClose$ = new Subject<boolean>();
  @Input() tdsDropdownMenu: TDSDropdownMenuComponent | null = null;
  @Input() trigger: 'click' | 'hover' = 'hover';
  @Input() matchWidthElement: ElementRef | null = null;
  @Input() @InputBoolean() backdrop = false;
  @Input() @InputBoolean() clickHide = true;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() visible = false;
  @Input() @InputBoolean() autoClose = true;
  @Input() overlayClassName: string = '';
  @Input() overlayStyle: IndexableObject = {};
  @Input() placement: TDSPlacementType = 'bottomLeft';
  @Output() readonly visibleChange: EventEmitter<boolean> = new EventEmitter();
  @Output() readonly tdsDropdownOutsideClick: EventEmitter<void> = new EventEmitter();
  setDropdownMenuValue<T extends keyof TDSDropdownMenuComponent>(key: T, value: TDSDropdownMenuComponent[T]): void {
    if (this.tdsDropdownMenu) {
      this.tdsDropdownMenu.setValue(key, value);
    }
  }

  constructor(
    public elementRef: ElementRef,
    private overlay: Overlay,
    private renderer: Renderer2,
    private viewContainerRef: ViewContainerRef,
    private platform: Platform
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-dropdown-trigger');
  }

  ngOnInit(): void { }

  ngAfterViewInit(): void {

    if (this.tdsDropdownMenu) {
      const nativeElement: HTMLElement = this.elementRef.nativeElement;
      /** host mouse state **/
      const hostMouseState$ = merge(
        fromEvent(nativeElement, 'mouseenter').pipe(mapTo(true)),
        fromEvent(nativeElement, 'mouseleave').pipe(mapTo(false))
      );
      /** menu mouse state **/
      const menuMouseState$ = this.tdsDropdownMenu.mouseState$;
      /** merged mouse state **/
      const mergedMouseState$ = merge(menuMouseState$, hostMouseState$);
      /** host click state **/
      const hostClickState$ = fromEvent(nativeElement, 'click').pipe(map(() => !this.visible));
      /** visible state switch by trigger **/
      const visibleStateByTrigger$ = this.trigger$.pipe(
        switchMap(trigger => {
          if (trigger === 'hover') {
            return mergedMouseState$;
          } else if (trigger === 'click') {
            return hostClickState$;
          } else {
            return EMPTY;
          }
        })
      );
      const descendantMenuItemClick$ = this.tdsDropdownMenu.descendantMenuItemClick$.pipe(
        filter(() => this.clickHide),
        mapTo(false)
      );
      const domTriggerVisible$ = merge(visibleStateByTrigger$, descendantMenuItemClick$, this.overlayClose$).pipe(
        filter(() => !this.disabled)
      );
      const visible$ = merge(this.inputVisible$, domTriggerVisible$);
      combineLatest([visible$, this.tdsDropdownMenu.isChildSubMenuOpen$])
        .pipe(
          map(([visible, sub]) => visible || sub),
          auditTime(150),
          distinctUntilChanged(),
          filter(() => this.platform.isBrowser),
          takeUntil(this.destroy$)
        )
        .subscribe((visible: boolean) => {
          const element = this.matchWidthElement ? this.matchWidthElement.nativeElement : nativeElement;
          const triggerWidth = element.getBoundingClientRect().width;
          if (this.visible !== visible) {
            this.visibleChange.emit(visible);
          }
          this.visible = visible;
          if (visible) {
            /** set up overlayRef **/
            if (!this.overlayRef) {
              /** new overlay **/
              this.overlayRef = this.overlay.create({
                positionStrategy: this.positionStrategy,
                minWidth: triggerWidth,
                disposeOnNavigation: true,
                hasBackdrop: (this.backdrop) && this.trigger === 'click',
                scrollStrategy: this.autoClose ? this.overlay.scrollStrategies.close() : this.overlay.scrollStrategies.reposition()
              });
              merge(
                this.overlayRef.backdropClick(),
                this.overlayRef.detachments(),
                this.overlayRef.outsidePointerEvents().pipe(filter((e: MouseEvent) => !this.elementRef.nativeElement.contains(e.target))),
                this.overlayRef.keydownEvents().pipe(filter(e => e.keyCode === ESCAPE && !hasModifierKey(e)))
              )
                .pipe(
                  filter(() => this.autoClose),
                  takeUntil(this.destroy$))
                .subscribe(() => {
                  this.overlayClose$.next(false);
                });
              merge(
                this.overlayRef.backdropClick(),
                this.overlayRef.outsidePointerEvents().pipe(filter((e: MouseEvent) => !this.elementRef.nativeElement.contains(e.target))),
              )
                .pipe(
                  takeUntil(this.destroy$))
                .subscribe(() => {
                  this.tdsDropdownOutsideClick.next();
                });
            } else {
              /** update overlay config **/
              const overlayConfig = this.overlayRef.getConfig();
              overlayConfig.minWidth = triggerWidth;
            }
            /** open dropdown with animation **/
            this.positionStrategy.withPositions([POSITION_MAP[this.placement], ...listOfPositions]);
            /** reset portal if needed **/
            if (!this.portal || this.portal.templateRef !== this.tdsDropdownMenu!.templateRef) {
              this.portal = new TemplatePortal(this.tdsDropdownMenu!.templateRef, this.viewContainerRef);
            }
            this.overlayRef.attach(this.portal);
          } else {
            /** detach overlayRef if needed **/
            if (this.overlayRef) {
              this.overlayRef.detach();
            }
          }
        });

      this.tdsDropdownMenu!.animationStateChange$.pipe(takeUntil(this.destroy$)).subscribe(event => {
        if (event.toState === 'void') {
          if (this.overlayRef) {
            this.overlayRef.dispose();
          }
          this.overlayRef = null;
        }
      });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    if (this.overlayRef) {
      this.overlayRef.dispose();
      this.overlayRef = null;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { visible, disabled, overlayClassName, overlayStyle, trigger, backdrop } = changes;
    if (trigger) {
      this.trigger$.next(this.trigger);
    }
    if (visible) {
      this.inputVisible$.next(this.visible);
    }
    if (disabled) {
      const nativeElement = this.elementRef.nativeElement;
      if (this.disabled) {
        this.renderer.setAttribute(nativeElement, 'disabled', '');
        this.inputVisible$.next(false);
      } else {
        this.renderer.removeAttribute(nativeElement, 'disabled');
      }
    }
    if (overlayClassName) {
      this.setDropdownMenuValue('overlayClassName', this.overlayClassName);
    }
    if (overlayStyle) {
      this.setDropdownMenuValue('overlayStyle', this.overlayStyle);
    }
    if (backdrop) {
      // console.warn('`backdrop` in dropdown component will be removed in 12.0.0, please use `hasBackdrop` instead.');
    }
  }
}
