

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  ChangeDetectorRef,
  ContentChildren,
  Directive,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  SimpleChanges
} from '@angular/core';
import { Subject } from 'rxjs';
import { InputBoolean } from 'tds-ui/shared/utility';

import { TDSMenuService } from 'tds-ui/menu';

@Directive({
  selector: '[tds-dropdown-item]',
  exportAs: 'tdsDropdownItem',
  host: {
    class:"tds-dropdown-item text-neutral-1-900  dark:text-d-neutral-1-900  text-body-2 px-3 py-1.5 font-regular dark:hover:bg-d-neutral-3-400 hover:bg-neutral-3-50  w-full",
    '[class.cursor-pointer]':"!disabled",
    '(click)': 'clickMenuItem($event)'
  }
})
export class TDSDropDownItemDirective implements OnInit, OnChanges, OnDestroy, AfterContentInit {
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_selected: BooleanInput;

  private destroy$ = new Subject<void>();
  selected$ = new Subject<boolean>();

  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() selected = false;

  /** clear all item selected status except this */
  clickMenuItem(e: MouseEvent): void {
    
    if (this.disabled) {
      e.preventDefault();
      e.stopPropagation();
    } else {
      this.TDSMenuService.onDescendantMenuItemClick(this);    
    }
  }

 




  constructor(
    private TDSMenuService: TDSMenuService,
    private cdr: ChangeDetectorRef,    
  ) {
    
  }

  ngOnInit(): void {
 
  }

  ngAfterContentInit(): void {
   
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.disabled) {
      this.cdr.markForCheck();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
